@import "reset";
@import "vars";

.wrapper {
  width: 100%;
  height: 100vh;
  padding-left: 1%;
  padding-right: 1%;
  overflow-x: hidden;
  font-family: $font-primary;
  background-image: url("../img/bg.jpg");
  background-size: cover;
  text-align: center;
}
* {
  box-sizing: border-box;
}

// common styles
.timeline {
  margin: 43px auto 30px;
  .timeline__circle {
    display: inline-block;
    width: 48px;
    height: 48px;
    line-height: 48px;
    color: #e5e4e2;
    font-size: 20px;
    border: 1px solid #e5e4e2;
    border-radius: 50%;
  }
  .timeline__check {
    display: none;
  }
  .timeline__progress {
    display: inline-block;
    width: 11.56vw;
    height: 2px;
    margin: 0 -4px 5px -3.8px;
    background-color: #e5e4e2;
    z-index: -1;
  }
  @media screen and (max-width: 967px) {
      .timeline__progress {
          width: 20vw;
      }
  }
}

.content {
  max-width: 945px;
  margin: 0 auto;
  padding: 39px 67px 30px 68px;
  background-color: white;
  box-shadow: 0 0 12px rgba(2, 3, 3, 0.1);
  overflow: hidden;

  // title
  .header__title {
    margin-bottom: 30px;
    font-size: 30px;
    color: #3c5c82;
    text-transform: uppercase;
  }
  .header__subtitle {
    font-size: 24px;
    color: #3c5c82;
    font-weight: 400;
  }

  // main content
  &__main {
    margin-top: 33px;
  }

// all buttons
  button {
    height: 59px;
    line-height: 59px;
    color: white;
    font-family: $font-primary;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: #3c5c82;
    border: none;
    box-sizing: border-box;
    &:hover {
      cursor: pointer;
      background-color: lighten(#3c5c82, 10%);
    }
  }

}

// common styles end

// CUSTOM STYLE TO EACH STAGE
// STAGE 0
.stage--0 {
  .timeline {
    display: none;
  }
  .content {
    margin-top: 122px;
    padding-bottom: 21px;

    .header__title {
      margin-top: 12px;
      margin-bottom: 2px;
      font-size: 40px;
      font-weight: bold;
    }
    .header__subtitle {
      font-size: 24px;
      color: #3c5c82;
      font-weight: 400;
    }
    .content__main {
    margin-top: 60px;
    }
    .content__question-container {
      display: none;
    }
  }

  .main__find {
    margin: 0 auto;

    .main__address {
      height: 59px;
      line-height: 59px;
      padding-left: 20px;
      padding-right: 11.3vw;
      margin-right: 0;
      color: #050505;
      font-size: 18px;
      font-weight: 400;
      border: 1px solid #e5e4e2;
    }
    .main__search {
      height: 62px;
      line-height: 62px;
      margin-left: -6px;
      padding: 0px 5.1vw;
    }

  }

  .content__footer {
    margin-top: 140px;
    text-align: left;

    .client__text {
      padding: 0 60px 15px 30px;
      text-align: left;
      color: #050505;
      font-size: 18px;
      font-weight: 400;
      line-height: 26px;
      border-bottom: 3px solid rgba(63, 61, 61, 0.5);
    }
    .client__profile {
      display: flex;
      align-items: center;
    }
    .client__avatar {
      height: 70px;
      width: 70px;
      margin: 14px;
      // background-image: url(../img/avatar_icon.png);
      overflow: hidden;
      border-radius: 50%;
    }
    .client__author {

    }
    .client__name {
      color: #3c5c82;
      font-size: 20px;
      font-weight: 400;
      line-height: 26px;
    }
    .client__subtitle {
      margin-top: -4px;
        color: #050505;
        font-family: Roboto;
        font-size: 16px;
        line-height: 26px;
    }

  }
  @media screen and (max-width: 945px) {
    .content {
      padding: 45px 30px 30px 30px;
    }
  }
  @media screen and (max-width: 768px) {
    .content__main {
      display: flex;
      justify-content: center;
      .main__address {
        padding-right: 25vw;
      }

      .main__search {
        margin-top: 30px;
        padding: 0px 10vw;
      }
    }
    .content__footer {
      margin-top: 100px;
      .client__text {
        padding: 0 0 20px 0;

      }
    }
  }

}

// STAGE 1
.stage--1 {
  .timeline {
    .timeline__circle--one {
      color: #3f3d3d;
      border: 2px solid #3f3d3d;
    }
  }
  .content {
    padding-left: 0;
    padding-right: 0;
  }
  #map {
      width: 100%;
      height: 377px;
      position: relative;
  }
  .content__footer {
    margin-top: 26px;
    padding-left: 45px;
    padding-right: 30px;
  }
  .content__question-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    .content__question {
      max-width: 38%;
      color: #050505;
      font-size: 18px;
      line-height: 22px;
      text-align: left;
    }
    .content__answer {
      padding-left: 100px;
      padding-right: 100px;

      &--no {
        background-color: #6b6b6b;
        margin-right: -57px;
        &:hover {
          background-color: lighten(#6b6b6b, 10%);
        }
      }
      &--yes {      }
    }
    @media screen and (max-width: 945px) {
      .content__question {
        max-width: 100%;
      }
      .content__answer {
        display: inline-block;
        margin-top: 30px;
        padding: 0 12vw;
        &--no {
          margin-right: 0;
        }
      }

    }
  }
  @media screen and (max-width: 945px) {
    .content__question-container {
      justify-content: space-around;
    }
  }

}

// common styles for stage2 and stage3
.content__details {

  .details__column {
    display: flex;
    text-align: left;
    label {
      display: block;
      margin-bottom: 9px;
      font-family: $font-primary;
      color: #3c5c82;
    }
    input, select {
      display: block;
      width: 99%;
      height: 40px;
      margin-bottom: 17px;
      padding-left: 10px;
      font-family: $font-primary;
      font-size: 18px;
      color: #898989;
      background-color: #fff;
      box-sizing: border-box;
    }

    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      background-image: url("../img/select__arrow.png") ;
      background-position: calc(100% - 9px);
      background-size: 17px 12px;
      background-repeat: no-repeat;
      &:hover {
        cursor: pointer;
      }
    }

    .details__column--one,
    .details__column--two {
      width: 49%;
    }
    .details__column--two {
      margin-left: 30px;
    }


  }

  .details__submit {
    margin: 27px auto 16px;
    padding: 0 38px;
    text-transform: none;
    span {
      color: #cbcbcb;
      font-size: 14px;
    }
  }

  @media screen and (max-width: 768px) {
    .details__column {
      flex-wrap: wrap;
      .details__column--one,
      .details__column--two {
        width: 100%;
      }
      .details__column--two {
        margin-left: 0;
      }
    }
    .details__submit {
      margin-top: 45px!important;
    }
  }
}
// end of common styles for stage2 and stage3

// STAGE 2
.stage--2 {
  .timeline {
    .timeline__circle--one {
      border: none;
      background-color: #3c5c82;
      .timeline__number--one {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }

    .timeline__circle--two {
      color: #3f3d3d;
      border: 2px solid #3f3d3d;
    }
  }
  .content__main {
    margin-top: 34px;
    font-size: 18px;
}

}

// STAGE 3
.stage--3 {
  .timeline {
    .timeline__circle--one {
      border: none;
      background-color: #3c5c82;
      .timeline__number--one {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }
    .timeline__circle--two {
      border: none;
      background-color: #3c5c82;
      .timeline__number--two {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }
    .timeline__circle--three {
      color: #3f3d3d;
      border: 2px solid #3f3d3d;
    }
  }

    .content__main {
      margin-top: 35px;
  }
  .content .header__title {
    margin-bottom: 0px;
  }

  .details__submit {
    height: 70px;
    line-height: 15px;
    margin-top: 177px;
    margin-bottom: 25px;
  }
}
